:root{
    /* ### Primary Colors */
    --dark-cyan: hsl(158, 36%, 37%);
    --cream: hsl(30, 38%, 92%);
    --very-dark-cyan:hsl(158deg 36.91% 16.45%);

    /* Neutral Colors */
    --very-dark-blue: hsl(212, 21%, 14%);
    --dark-grayish-blue: hsl(228, 12%, 48%);
    --white: hsl(0, 0%, 100%);

    /* Typography */
    --font-size-paragraph: 14px;
    --heading-font: "Fraunces";
    --paragraph-font: "Monsterrat";
}
